___CSS_LOADER_EXPORT___.push([module.id, `:root {
  --primary-color: rgb(17, 86, 102);
  --primary-color-darker: rgb(9, 48, 56);
  
  --primary-box-color: rgb(255, 217, 145);
  --secondary-box-color: rgb(27, 144, 170);
  
  --primary-box-font-color: rgb(110, 70, 1);
  --secondary-box-font-color: rgb(16, 45, 52);

  --error-color: rgb(156, 6, 6);
}

* {
  box-sizing: border-box;
  outline: 0;
}

body {
  margin: 0;
  padding: 0;
  background: var(--primary-color);
  font-family: 'Open sans', sans-serif;
  font-size: 1.3em;
  line-height: 1.5em;
}

.container {
  max-width: 640px;
  margin: 50px auto;
  background: #fff;
  padding: 20px;
  border-radius: 10px;
}

form input, form label, form button {
  display: block;
  width: 100%;
  margin-bottom: 10px;
}

form input {
  font-size: 24px;
  height: 50px;
  padding: 0 20px;
}

form input:focus {
  outline: 1px solid var(--primary-color);
}

form button {
  border: none;
  background: var(--primary-color);
  color: #fff;
  font-size: 18px;
  font-weight: 700;
  height: 50px;
  cursor: pointer;
  margin-top: 30px;
}

form button:hover {
  background: var(--primary-color-darker);
}

.error-message {
  color: var(--error-color);
  font-size: 1rem;
  font-weight: 700;
}

.cpf-gerado p {
  font-size: 2em;
  font-weight: 700;
  text-align: center;
  border: 1px solid black;
  padding: 5px 0;
  background-color: var(--primary-box-color);
  border-radius: 5px;
}

.cpf-gerado p:hover {
  background-color: var(--secondary-box-color);
